import React from "react";
import styles from "./styles.module.scss";
import productService from "@/app/services/product";
import Product from "@/app/types/product";
import ImageView from "./ImageView";
import { classes } from "@/app/utils/css";
import VariantPrice from "@/app/types/variantPrice";

async function ProductPage({ params }) {
    const product: Product | null = await productService.getProduct(params?.handle);

    if (product === null) {
        return (
            <div className={styles.page}>
                <h4>Product not found!</h4>
            </div>
        );
    }

    const { amount, currencyCode }: VariantPrice = product?.priceRange.minVariantPrice;

    return (
        <div className={styles.page}>
            <div className="container-fluid ps-0 pe-0">
                <div className="row">
                    <div className="col-lg-4">
                        <div className={styles["card-view"]}>
                            <ImageView product={product} />
                        </div>
                    </div>
                    <div className="col-lg-5">
                        <div className={styles["card-view"]}>
                            <div className={classes(styles, "prod-info-view__title")}>{product.title}</div>
                            <div className={classes(styles, "prod-info-view__price")}>
                                {amount} {currencyCode}
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-3">321</div>
                </div>
            </div>
        </div>
    );
}

export default ProductPage;
